import { useQuery } from '@tanstack/react-query';
import { ApiType, CoreClient, TargetDrive } from '@youfoundation/transit-lib';
import useAuth from '../auth/useAuth';

const useDrive = ({ targetDrive }: { targetDrive: TargetDrive }) => {
  const { getSharedSecret } = useAuth();
  const client = new CoreClient({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

  const fetch = async (targetDrive: TargetDrive) => {
    const allDrives = await (
      await client.driveProvider.GetDrives({ pageNumber: 1, pageSize: 100 })
    ).results;

    return allDrives.find(
      (drive) =>
        drive.targetDriveInfo.alias === targetDrive.alias &&
        drive.targetDriveInfo.type === targetDrive.type
    );
  };

  return {
    fetch: useQuery(
      ['drive', `${targetDrive.alias}_${targetDrive.type}`],
      () => fetch(targetDrive),
      {
        refetchOnWindowFocus: false,
      }
    ),
  };
};

export default useDrive;
