import { useQuery } from '@tanstack/react-query';
import { CircleProvider } from '../../provider/connections/CircleProvider';
import useAuth from '../auth/useAuth';

const useCircles = () => {
  const { getSharedSecret } = useAuth();
  const circleProvider = CircleProvider.getInstance(getSharedSecret());

  const fetchAll = async () => {
    const circles = await circleProvider.getCircles();
    return circles?.sort((a, b) => (a.disabled ? 1 : 0) - (b.disabled ? 1 : 0));
  };

  return {
    fetch: useQuery(['circles'], () => fetchAll(), {
      refetchOnWindowFocus: false,
    }),
  };
};

export default useCircles;
