import { DriveDefinition } from '@youfoundation/transit-lib';
import { t } from '../../../../helpers/i18n/dictionary';
import useApps from '../../../../hooks/apps/useApps';
import useCircles from '../../../../hooks/circles/useCircles';
import { drivePermissionLevels } from '../../../../provider/permission/permissionLevels';
import AppMembershipView from '../../../PermissionViews/AppPermissionView/AppPermissionView';
import CirclePermissionView from '../../../PermissionViews/CirclePermissionView/CirclePermissionView';
import Section from '../../../Sections/Section';
import { getAccessFromPermissionNumber } from '../../DemoData/helpers';

const DrivePermissionOverview = ({ driveDefinition }: { driveDefinition: DriveDefinition }) => {
  const { data: circles } = useCircles().fetch;
  const { data: apps } = useApps().fetchRegistered;

  const targetDriveInfo = driveDefinition.targetDriveInfo;

  const circlesWithAGrantOnThis = circles?.filter((circle) =>
    circle.driveGrants?.some(
      (grant) =>
        grant.permissionedDrive.drive.alias === targetDriveInfo.alias &&
        grant.permissionedDrive.drive.type === targetDriveInfo.type
    )
  );

  const appsWithAGrantOnThis = apps?.filter((app) =>
    app.grant.driveGrants.some(
      (grant) =>
        grant.permissionedDrive.drive.alias === targetDriveInfo.alias &&
        grant.permissionedDrive.drive.type === targetDriveInfo.type
    )
  );

  return (
    <>
      {circlesWithAGrantOnThis?.length ? (
        <Section title={t('Circles with access:')} isOpaqueBg={true}>
          <ul className="-my-4">
            {circlesWithAGrantOnThis.map((circle) => {
              const matchingGrant = circle.driveGrants.find(
                (grant) =>
                  grant.permissionedDrive.drive.alias === targetDriveInfo.alias &&
                  grant.permissionedDrive.drive.type === targetDriveInfo.type
              );
              return (
                <CirclePermissionView
                  circleDef={circle}
                  key={circle.id}
                  permissionLevel={
                    getAccessFromPermissionNumber(
                      matchingGrant?.permissionedDrive?.permission,
                      drivePermissionLevels
                    ).name
                  }
                  className="my-4"
                />
              );
            })}
          </ul>
        </Section>
      ) : null}

      {appsWithAGrantOnThis?.length ? (
        <Section title={t('Apps with access:')} isOpaqueBg={true}>
          <ul className="-my-4">
            {appsWithAGrantOnThis.map((app) => {
              const matchingGrant = app.grant.driveGrants.find(
                (grant) =>
                  grant.permissionedDrive.drive.alias === targetDriveInfo.alias &&
                  grant.permissionedDrive.drive.type === targetDriveInfo.type
              );
              return (
                <AppMembershipView
                  className="my-4"
                  appDef={app}
                  key={app.appId}
                  permissionLevel={
                    getAccessFromPermissionNumber(
                      matchingGrant?.permissionedDrive.permission,
                      drivePermissionLevels
                    ).name
                  }
                />
              );
            })}
          </ul>
        </Section>
      ) : null}
    </>
  );
};

export default DrivePermissionOverview;
