import {
  AppClientRegistrationRequest,
  AppClientRegistrationResponse,
  AppRegistrationRequest,
  RedactedAppRegistration,
  GetAppRequest,
} from './AppManagementProviderTypes';
import { OwnerProviderBase } from '../OwnerProviderBase';

export default class AppManagementProvider extends OwnerProviderBase {
  private static instance: AppManagementProvider;
  constructor(sharedSecret: Uint8Array | undefined) {
    super(sharedSecret);
  }

  //adds the specified client to the list of allowed clients for a given app; returns a CAT
  async RegisterAppClient(
    request: AppClientRegistrationRequest
  ): Promise<AppClientRegistrationResponse> {
    const client = this.createAxiosClient();
    const response = await client.post<AppClientRegistrationResponse>(
      'appmanagement/register/client',
      request
    );
    return response.data;
  }

  async RegisterChatAppClient_temp(
    request: AppClientRegistrationRequest
  ): Promise<AppClientRegistrationResponse> {
    const client = this.createAxiosClient();
    const response = await client.post<AppClientRegistrationResponse>(
      'appmanagement/register/chatclient_temp',
      request
    );
    console.log('RegisterChatAppClient_temp returning response');
    console.log(response);

    return response.data;
  }

  async RegisterApp(request: AppRegistrationRequest): Promise<RedactedAppRegistration> {
    const client = this.createAxiosClient();
    const response = await client.post<RedactedAppRegistration>(
      'appmanagement/register/app',
      request
    );

    console.log('RegisterApp returning response');
    console.log(response);

    return response.data;
  }

  async GetAppRegistration(request: GetAppRequest): Promise<RedactedAppRegistration> {
    const client = this.createAxiosClient();
    const response = await client.post<RedactedAppRegistration>('appmanagement/app', request);
    return response.data;
  }

  async GetAppRegistrations(): Promise<RedactedAppRegistration[]> {
    const client = this.createAxiosClient();
    const response = await client.get<RedactedAppRegistration[]>('appmanagement/list');
    return response.data;
  }

  async RevokeApp(request: GetAppRequest): Promise<void> {
    const client = this.createAxiosClient();
    await client.post('appmanagement/revoke', request);
  }

  async AllowApp(request: GetAppRequest): Promise<void> {
    const client = this.createAxiosClient();
    const response = await client.post('appmanagement/allow', request);
    console.log(response);
  }

  public static getInstance(sharedSecret): AppManagementProvider {
    if (!AppManagementProvider.instance) {
      AppManagementProvider.instance = new AppManagementProvider(sharedSecret);
    }

    return AppManagementProvider.instance;
  }
}
