import { useState } from 'react';
import { useParams } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useDrive from '../../../hooks/drive/useDrive';
import ActionButton from '../../Buttons/ActionButton';
import DriveAccessDialog from '../../Dialog/DriveAccessDialog/DriveAccessDialog';
import DrivePermissionOverview from '../Drives/DrivePermissionOverview/DrivePermissionOverview';
import HardDrive from '../../Icons/HardDrive/HardDrive';
import PageMeta from '../../Layout/PageMeta/PageMeta';
import Section from '../../Sections/Section';
import LoadingDetailPage from '../../Loaders/LoadingDetailPage/LoadingDetailPage';

const DriveDetails = () => {
  const { driveKey } = useParams();
  const splittedDriveKey = driveKey.split('_');
  const {
    fetch: { data: driveDef, isLoading: driveDefLoading },
  } = useDrive({ targetDrive: { alias: splittedDriveKey[0], type: splittedDriveKey[1] } });

  const [isEditAccess, setIsEditAccess] = useState(false);

  if (driveDefLoading) {
    return <LoadingDetailPage />;
  }

  if (!driveDef) {
    return <>No matching drive found</>;
  }

  return (
    <>
      <PageMeta
        icon={HardDrive}
        title={`${driveDef.name}`}
        actions={
          <>
            <ActionButton onClick={() => setIsEditAccess(true)} className="m-2">
              Edit Access
            </ActionButton>
          </>
        }
        breadCrumbs={[
          { href: '/owner/drives', title: 'My Drives' },
          { title: driveDef.name ?? '' },
        ]}
      />
      <Section title={t('Metadata')} isOpaqueBg={true}>
        <p>{driveDef.metadata}</p>
        <ul>
          {driveDef.allowAnonymousReads ? <li>Allow Anonymous Reads</li> : null}
          {driveDef.isReadonly ? <li>Read Only</li> : null}
        </ul>
      </Section>

      <DrivePermissionOverview driveDefinition={driveDef} />

      <DriveAccessDialog
        driveDefinition={driveDef}
        isOpen={isEditAccess}
        onCancel={() => {
          setIsEditAccess(false);
        }}
        onConfirm={() => {
          setIsEditAccess(false);
        }}
        title={`${t('Edit access on')} ${driveDef.name}`}
      />
    </>
  );
};

export default DriveDetails;
