import { DriveDefinition } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { createPortal } from 'react-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useCircle from '../../../hooks/circle/useCircle';
import useCircles from '../../../hooks/circles/useCircles';
import usePortal from '../../../hooks/portal/usePortal';
import { CircleDefinition } from '../../../provider/connections/CircleDataTypes';
import { drivePermissionLevels } from '../../../provider/permission/permissionLevels';
import ActionButton, { ActionButtonState } from '../../Buttons/ActionButton';
import PermissionLevelEditor from '../../Form/PermissionLevelEditor';
import Circles from '../../Icons/Circles/Circles';
import DialogWrapper from '../Common/DialogWrapper';

const DriveAccessDialog = ({
  title,
  confirmText,

  isOpen,
  driveDefinition,

  onConfirm,
  onCancel,
}: {
  title: string;
  confirmText?: string;

  isOpen: boolean;
  driveDefinition: DriveDefinition;

  onConfirm: () => void;
  onCancel: () => void;
}) => {
  const target = usePortal('modal-container');

  const targetDriveInfo = driveDefinition.targetDriveInfo;

  const [saveState, setSaveState] = useState<ActionButtonState>('idle');

  const { data: circles } = useCircles().fetch;
  const { mutateAsync: updateCircle } = useCircle({}).createOrUpdate;

  // TODO: Add apps as well
  // const { data: apps } = useApps().fetchRegistered;

  const [newCircles, setNewCircles] = useState<CircleDefinition[]>([]);

  if (!isOpen) {
    return null;
  }

  const dialog = (
    <DialogWrapper title={title} onClose={onCancel}>
      <>
        <form
          onSubmit={async (e) => {
            e.preventDefault();
            setSaveState('loading');

            try {
              await Promise.all(newCircles.map(async (circle) => await updateCircle(circle)));
            } catch (ex) {
              setSaveState('error');
            }
            setSaveState('success');

            onConfirm();
            return false;
          }}
        >
          {circles?.length ? (
            <>
              <h2 className="mb-2 flex flex-row text-xl">
                <Circles className="my-auto mr-2 h-4 w-4" /> {t('Circles')}:
              </h2>
              {circles.map((circle) => {
                const matchingNewCircle = newCircles?.find(
                  (newCircle) => newCircle.id === circle.id
                );

                const driveGrant = (matchingNewCircle ?? circle).driveGrants?.find(
                  (driveGrant) =>
                    driveGrant.permissionedDrive.drive.alias === targetDriveInfo.alias &&
                    driveGrant.permissionedDrive.drive.type === targetDriveInfo.type
                );

                return (
                  <div className="mb-5 flex flex-row" key={circle.id}>
                    <label htmlFor="name" className="my-auto mr-2">
                      {circle.name}
                    </label>
                    <PermissionLevelEditor
                      className="ml-auto"
                      permissionLevels={drivePermissionLevels}
                      defaultValue={driveGrant?.permissionedDrive.permission}
                      onChange={(newValue) => {
                        setNewCircles([
                          ...newCircles.filter((circleToUpdate) => circleToUpdate.id !== circle.id),
                          {
                            ...circle,

                            driveGrants: [
                              ...circle.driveGrants.filter(
                                (driveGrant) =>
                                  driveGrant.permissionedDrive.drive.alias !==
                                    targetDriveInfo.alias &&
                                  driveGrant.permissionedDrive.drive.type !== targetDriveInfo.type
                              ),
                              {
                                permissionedDrive: {
                                  drive: { ...targetDriveInfo },
                                  permission: newValue,
                                },
                              },
                            ],
                          },
                        ]);
                      }}
                    />
                  </div>
                );
              })}
            </>
          ) : null}
          <div className="-m-2 flex flex-row-reverse py-3">
            <ActionButton className="m-2" icon={'send'} state={saveState}>
              {confirmText || t('Save')}
            </ActionButton>
            <ActionButton className="m-2" type="secondary" onClick={onCancel}>
              {t('Cancel')}
            </ActionButton>
          </div>
        </form>
      </>
    </DialogWrapper>
  );

  return createPortal(dialog, target);
};

export default DriveAccessDialog;
